.header {
    display: flex;
    justify-content: space-between;
    border: 1px solid black;
}

.logo {
    width: 200px;
}

.nav-items {
    padding: 0px 20px;
}

.nav-items>ul {
    display: flex;
    font-size: 24px;
    list-style: none;

}

.nav-items>ul>li {
    padding: 10px;
    margin: 10px;
}

.res-card {
    width: 200px;
    padding: 5px;
}

.res-card:hover {
cursor: pointer;
    border: 1px solid black;
}

.search {
    padding: 10px;
}

.res-logo {
    width: 190px;
}